.maintenance-section {
  padding: 40px 0;
  .maintenance-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 20px;
    .image-box {
      height: 260px;
      justify-content: center;
      align-items: center;
      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
    .title-box {
      display: flex;
      flex-direction: row;
      label {
        margin-bottom: 0;
        margin-right: 10px;
        cursor: pointer;
        body.ar & {
          margin-right: unset;
          margin-left: 10px;
        }
        position: relative;
        margin-bottom: 3px;
        color: #919294;
        i {
          font-size: 25px;
          transition: 0.3s;
          opacity: 1;
          backface-visibility: hidden;
        }
        i.fa-check-square {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          opacity: 0;
          transition: 0.5s;
        }
      }
      input {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        &:checked ~ label i {
          opacity: 0;
        }
        &:checked ~ label i.fa-check-square {
          opacity: 1;
        }
      }
    }
  }
}
